<template>
    <div>
        <div class="vx-row">
            <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
              <div class="vx-card overflow-hidden mb-base">
                <div class="vx-card__collapsible-content vs-con-loading__container"><div>
                    <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                      <span class="p-3 inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-primary),0.15);">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-map-pin "><path d="M21 10c0 7-9 13-9 13s-9-6-9-13a9 9 0 0 1 18 0z"></path><circle cx="12" cy="10" r="3"></circle>
                        </svg>
                      </span>
                      <div class="truncate">
                        <h2 class="mb-1 font-bold">{{stockpoint}}</h2>
                        <span>Stockpoint</span>
                      </div>
                    </div>
                  </div>
                </div> 
              </div>
            </div>
            <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
              <div class="vx-card overflow-hidden mb-base">
                <div class="vx-card__collapsible-content vs-con-loading__container"><div>
                    <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                       <span class="p-3 inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-primary),0.15);"><svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-user "><path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path><circle cx="12" cy="7" r="4"></circle></svg></span>
                      <div class="truncate">
                        <h2 class="mb-1 font-bold">{{reseller}}</h2>
                        <span>Reseller</span>
                      </div>
                    </div>
                  </div>
                </div> 
              </div>
            </div>
            <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
              <div class="vx-card overflow-hidden mb-base">
                <div class="vx-card__collapsible-content vs-con-loading__container"><div>
                    <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                     <span class="p-3 inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-primary),0.15);"><svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-users "><path d="M17 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2"></path><circle cx="9" cy="7" r="4"></circle><path d="M23 21v-2a4 4 0 0 0-3-3.87"></path><path d="M16 3.13a4 4 0 0 1 0 7.75"></path></svg></span>
                      <div class="truncate">
                        <h2 class="mb-1 font-bold">{{customer}}</h2>
                        <span>Customer</span>
                      </div>
                    </div>
                  </div>
                </div> 
              </div>
            </div>
            
            
        </div>

        <vx-card class="mb-base">
            <apexchart type="line" height="350" :options="chartOptions" :series="series"></apexchart>
        </vx-card>

    </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'
import StatisticsCardLine from '@/components/statistics-cards/StatisticsCardLine.vue'

export default {
  data() {
    return {
      stockpoint:'',
      reseller:'',
      customer:'',
      siteTrafficChartData: {
          series: [{
              name: 'Traffic Rate',
              data: [150, 200, 125, 225, 200, 250]
          }],
          chartOptions: {
              grid: {
                  show: false,
                  padding: {
                      left: 0,
                      right: 0
                  }
              },
              chart: {
                  type: 'line',
                  dropShadow: {
                      enabled: true,
                      top: 5,
                      left: 0,
                      blur: 4,
                      opacity: 0.10,
                  },
                  toolbar: {
                      show: false,
                  },
                  sparkline: {
                      enabled: true
                  }
              },
              stroke: {
                  width: 5,
                  curve: 'smooth'
              },
              xaxis: {
                  type: 'numeric',
              },
              colors: ['#7367F0'],
              fill: {
                  type: 'gradient',
                  gradient: {
                      shade: 'dark',
                      gradientToColors: ['#A9A2F6'],
                      shadeIntensity: 1,
                      type: 'horizontal',
                      opacityFrom: 1,
                      opacityTo: 1,
                      stops: [0, 100, 100, 100]
                  },
              },
              markers: {
                  size: 0,
                  hover: {
                      size: 5
                  }
              },
              tooltip: {
                  x: { show: false }
              },
          }
      },
      series: [
                    {
                      name: "Bulan Ini",
                      data: [28, 29, 33, 36, 32, 32, 33]
                    },
                    {
                      name: "Bulan Lalu",
                      data: [12, 11, 14, 18, 17, 13, 13]
                    }
      ],
      chartOptions: {
            chart: {
              height: 350,
              type: 'line',
              dropShadow: {
                enabled: true,
                color: '#000',
                top: 18,
                left: 7,
                blur: 10,
                opacity: 0.2
              },
              toolbar: {
                show: false
              }
            },
            colors: ['#77B6EA', '#545454'],
            dataLabels: {
              enabled: true,
            },
            stroke: {
              curve: 'smooth'
            },
            title: {
              text: 'GRAFIK PENJUALAN',
              align: 'left'
            },
            grid: {
              borderColor: '#e7e7e7',
              row: {
                colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                opacity: 0.5
              },
            },
            markers: {
              size: 1
            },
            xaxis: {
              categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul'],
              title: {
                text: 'Month'
              }
            },
            yaxis: {
              title: {
                text: 'Temperature'
              },
              min: 5,
              max: 40
            },
            legend: {
              position: 'top',
              horizontalAlign: 'right',
              floating: true,
              offsetY: -25,
              offsetX: -5
            }
      },
      
    }
  },

  components: {
    apexchart: VueApexCharts,
    StatisticsCardLine
  },

  mounted(){
      this.loadData();
  },

  methods:{
      loadData(){
          this.$http.get('agen/web/dashboard')
          .then(response => {
              this.stockpoint = response.data.message.stockpoint,
              this.reseller = response.data.message.reseller
              this.customer = response.data.message.customer
          })
          .catch(err=>
            this.$swal.fire(
              'Gagal !',
                err.response.data.message,
              'error'
            )
          )               
      }
  }
}
</script>